      left: "",
    },
    filters: {
      nuts: true,
      vegeterianOnly: false,
      category: "",
      speciesValue: 0,
    },
  }),

  methods: {
    arrowRight() {
      let carouselItemWidth =
        document.querySelector(".carousel__slide").offsetWidth;
      this.carouselStep -= carouselItemWidth;
      if (
        this.carouselStep ===
        -carouselItemWidth * (this.carouselList.length - 1)
      ) {
        this.showArrowRight = false;
      }
    },
    arrowLeft() {
      let carouselItemWidth =
        document.querySelector(".carousel__slide").offsetWidth;
      this.carouselStep += carouselItemWidth;
      if (
        this.carouselStep <=
        -carouselItemWidth * (this.carouselList.length - 2)
      ) {
        this.showArrowRight = true;
      }
    },
    leftRibbon() {
      document.querySelector(".ribbon__inner").scrollLeft += 350;
    },
    rightRibbon() {
      document.querySelector(".ribbon__inner").scrollLeft -= 350;
    },
    selectId(index) {
      this.categories.forEach((item) => (item.isActive = false));
      this.categories[index].isActive = true;

      this.products.filter(
        (item) => item.category === this.categories[index].id
      );
      this.filters.category = this.categories[index].id;
    },

    goStep(event) {
      const slider = document.querySelector(".slider");
      const sliderSegment = (1 / (this.sliderData.steps - 1)) * 100;
      let clickX = event.clientX - slider.getBoundingClientRect().x;
      this.sliderData.sliderValue = Math.round(
        (clickX * (this.sliderData.steps - 1)) / slider.clientWidth
      );
      this.sliderData.sliderThumb =
        sliderSegment * this.sliderData.sliderValue + "%";
      this.sliderData.sliderProgress =
        sliderSegment * this.sliderData.sliderValue + "%";
      this.sliderStep.forEach((el, idx) => {
        el.stepActive = false;
        if (idx === this.sliderData.sliderValue) {
          el.stepActive = true;
        }
      });
      this.filters.speciesValue = this.sliderData.sliderValue;
    },
    openYourOrder() {
      this.modal.open = true;
      this.cartStyle.zIndex = 0;
    },

    modalClose() {
      this.modal.open = false;
    },

    addProduct(index) {
      this.cart.isShake = true;
      setTimeout(() => {
        this.cart.isShake = false;
      }, 500);
      this.cart.count += 1;

      let x = this.products.findIndex(
        (i) => i.id === event.target.closest(".card").id
      );

      this.cart.price += this.products[x].price;

      let lastItem = this.productsInCart.findIndex(
        (item) => item.product.name == this.products[x].name
      );
      if (lastItem === -1) {
        this.productsInCart.push({
          product: {
            price: this.products[x].price,
            name: this.products[x].name,
            image: this.products[x].image,
          },
          count: 1,
        });
      } else {
        this.productsInCart[lastItem].count++;
        this.productsInCart[lastItem].product.price += this.products[x].price;
      }
    },

    addProductCarousel(index) {
      this.cart.isShake = true;
      setTimeout(() => {
        this.cart.isShake = false;
      }, 500);
      this.cart.count += 1;

      let x = this.products.findIndex(
        (i) => i.name === this.carouselList[index].title
      );
      this.cart.price += this.products[x].price;

      let lastItem = this.productsInCart.findIndex(
        (item) => item.product.name == this.products[x].name
      );
      if (lastItem === -1) {
        this.productsInCart.push({
          product: {
            price: this.products[x].price,
            name: this.products[x].name,
            image: this.products[x].image,
          },
          count: 1,
        });
      } else {
        this.productsInCart[lastItem].count++;
        this.productsInCart[lastItem].product.price += this.products[x].price;
      }
    },

    cartBtnMinus(index) {
      this.productsInCart[index].count -= 1;
      let x = this.products.findIndex(
        (i) => i.name === this.productsInCart[index].product.name
      );
      this.productsInCart[index].product.price -= this.products[x].price;
      if (this.productsInCart[index].count === 0) {
        this.productsInCart.splice(index, 1);
      }
      if (this.productsInCart.length === 0) {
        this.modal.open = false;
      }
    },

    cartBtnPlus(index) {
      this.productsInCart[index].count += 1;
      let x = this.products.findIndex(
        (i) => i.name === this.productsInCart[index].product.name
      );
      this.productsInCart[index].product.price += this.products[x].price;
    },

    updatePosition() {
      const cartIcon = document.querySelector(".cart-icon");
      this.cartStyle.position = "fixed";
      this.cartStyle.zIndex = 1000;
      if (this.modal.open) {
        this.cartStyle.zIndex = 0;
      }
      if (window.pageYOffset === 0) {
        this.cartStyle.position = "absolute";
      }

      if (document.documentElement.clientWidth <= 767) {
        this.cartStyle.top = "0px";
      } else {
        this.cartStyle.top = "50px";
      }
    },

    nutsFilter() {
      this.filters.nuts = !this.filters.nuts;
    },

    vegetarianFilter() {
      this.filters.vegeterianOnly = !this.filters.vegeterianOnly;
    },
  },

  computed: {
    showArrowLeft() {
      return this.carouselStep != 0 ? true : false;
    },
    isEmpty() {
      return this.productsInCart.length !== 0 ? true : false;
    },

    filterProducts() {
      if (
        this.filters.category === "" &&
        this.filters.nuts &&
        !this.filters.vegeterianOnly
      ) {
        return this.products.filter(
          (item) => item.spiciness >= this.filters.speciesValue
        );
      }
      if (
        this.filters.category === "" &&
        !this.filters.nuts &&
        !this.filters.vegeterianOnly
      ) {
        return this.products
          .filter((item) => item.spiciness >= this.filters.speciesValue)
          .filter((item) => item.nuts === false);
      }
      if (
        this.filters.category === "" &&
        this.filters.nuts &&
        this.filters.vegeterianOnly
      ) {
        return this.products
          .filter((item) => item.spiciness >= this.filters.speciesValue)
          .filter((item) => item.vegeterian === true);
      }
      if (
        this.filters.category === "" &&
        !this.filters.nuts &&
        this.filters.vegeterianOnly
      ) {
        return this.products
          .filter((item) => item.spiciness >= this.filters.speciesValue)
          .filter((item) => item.nuts === false)
          .filter((item) => item.vegeterian === true);
      }

      if (
        this.filters.category !== "" &&
        this.filters.nuts &&
        !this.filters.vegeterianOnly
      ) {
        return this.products
          .filter((item) => item.category === this.filters.category)
          .filter((item) => item.spiciness >= this.filters.speciesValue);
      }
      if (
        this.filters.category !== "" &&
        !this.filters.nuts &&
        !this.filters.vegeterianOnly
      ) {
        return this.products
          .filter((item) => item.category === this.filters.category)
          .filter((item) => item.spiciness >= this.filters.speciesValue)
          .filter((item) => item.nuts === false);
      }
      if (
        this.filters.category !== "" &&
        this.filters.nuts &&
        this.filters.vegeterianOnly
      ) {
        return this.products
          .filter((item) => item.category === this.filters.category)
          .filter((item) => item.spiciness >= this.filters.speciesValue)
          .filter((item) => item.vegeterian === true);
      }
      if (
        this.filters.category !== "" &&
        !this.filters.nuts &&
        this.filters.vegeterianOnly
      ) {
        return this.products
          .filter((item) => item.category === this.filters.category)
          .filter((item) => item.spiciness >= this.filters.speciesValue)
          .filter((item) => item.nuts === false)
          .filter((item) => item.vegeterian === true);
      }
    },

    totalCartCount() {
      return this.productsInCart.reduce((acc, curr) => acc + curr.count, 0);
    },

    totalCartPrice() {
      return this.productsInCart.reduce(
        (acc, curr) => acc + curr.product.price,
        0
      );
    },

    priceItemForm() {
      return this.totalPriceItem;
    },
  },

  mounted() {
    if (document.documentElement.clientWidth <= 767) {
      this.cartStyle.top = "0px";
    }
  },

  created() {
    document.addEventListener("scroll", () => this.updatePosition());
    window.addEventListener("resize", () => this.updatePosition());
  },
};
</script>
